.container {
  display: flex;
  justify-content: center;
  padding-top: 50vh;
  width: 100vw;
}

.button {
  display: inline-flex;
  color: #000;
  background-color: #d36431;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  font-weight: 500;
  user-select: none;
  transition: background-color 250ms ease-in-out, box-shadow 250ms ease-in-out;
}

.button[data-action="start"] {
  margin-right: 50px;
}

.button:hover {
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
  background-color: #d0d678;
}
